@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,400&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f8f7ff;
  overflow-y: hidden !important;
}

.App {
  display: flex;
}

legend {
  display: none;
}
.MuiInput-root {
  position: relative;
  height: 48px;
  width: 50%;
}

#Randomize-button {
  position: relative;
  bottom: 8.5%;
  height: 48px;
  width: 50%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 2px;
}
#Randomize-input {
  position: relative;
  left: 0;
  width: 100%;
  margin-bottom: -10px;
  padding-top: 5px;
  font-size: 14px;
  text-align: center;
}

.tiles__tile {
  background-color: #ffffff;
  color: #abb0b5;
  padding: 0px 20px;
  height: 138px;
  display: flex;
  justify-content: center;
  position: relative;
  border-radius: 10px;
  border: 1px solid #e4e7e9;
  box-shadow: 0px 10px 15px -10px rgba(0,0,0,0.72);
  cursor: pointer;
  width: 250px;
  margin: 0 auto;
}

.tiles__tile h3 {
  font-size: 18px;
  font-weight: 700;
  border-bottom: 1px solid #f8f7ff;
  padding: 10px 0px;
  z-index: 1000;
  position: absolute;
  background-color: #ffffff;
  top: 0;
  right: 15px;
  left: 15px;
  margin: 0;
}

.tiles__tile i {
  text-align: center;
  font-size: 40px;
  display: block;
  transition: .3s;
  z-index: 900;
  align-self: center;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tiles__tile p {
  transition: .3s;
  margin-top: 50px;
  overflow-y: scroll;
  text-align: justify;
  padding-right: 5px;
}

.tiles__tile p::-webkit-scrollbar{
  width: 4px;
}

.tiles__tile p::-webkit-scrollbar-thumb{
  border-width: 1px 1px 1px 1px;
  border-color: #45465b;
  background-color: #45465b;
  border-radius: 10px;
}

.tiles__tile:hover i {
  top: 15%;
}

.tiles__tile p {
  opacity: 0;
}

.tiles__tile:hover p {
  opacity: 1;
  display: block;
}

.tiles__tile:hover {
  background-color: #7fd3f7;
  color: #f8f7ff;
}

.tiles__tile:hover h3 {
  background-color: #7fd3f7;
}

.tiles__tile:hover p::-webkit-scrollbar-thumb {
  background-color: #f8f7ff;
}
.text {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* .completed {
  text-decoration: line-through;
} */

ul {
  list-style-type: none;
}

.doneButton {
  background-color: #ffc600;
}

#filterButton {
  background-color: #ffc600;
}

#deleteBtn {
  background-color: tomato;
}

.join-container {
    width: 400px;
    text-align: center;
}

.join-row {
    margin-bottom: 1rem;
}
.chat {
    height: 450px;
    width: 400px;
}

.chat-header {
    height: 50px;
    border-bottom: 1px solid #00000032;
    text-align: center;
}
.message-list {
    overflow: scroll;
    height: 293px;
}
.outgoing {
  text-align: end;
}

.incoming {
  text-align: start;
}
p {
    margin: 0;
    padding: 0;
}
    
.outgoing-message-text {
    color: #fff;
}
    
.outgoing-message-container {
    text-align: left;
    display: inline-block;
    max-width: 300px;
    background-color: #24c3f9;
    border-radius: .5rem;
    padding: .6rem;
    margin:  4px 16px;
}
p, h4 {
    margin: 0;
    padding: 0;
}

.incoming-message-user {
    color: #24c3f9;
    margin-bottom: 4px;
}

.incoming-message-container {
    display: inline-block;
    max-width: 300px;
    background-color: #00000010;
    border-radius: .5rem;
    padding: .6rem;
    margin:  4px 16px;
}


.add-message-bar {
    padding: 2rem 0;
    width: 100%;
    border-top: 1px solid #00000032;
    display: flex;
    justify-content: space-between;
}

.add-message-bar button {
    margin-left: 1rem;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
legend {
  display: none;
}
